// bibliotek
import { Link } from "react-router-dom";


//obj   
import objAside from "../../asset/obj/aside.js";//боковая часть страницы (новости в тренде)
import bilbordEda from "../../asset/obj/eda/bilbordEda.js"; //начальная билборд
import objEdaItem from "../../asset/obj/eda/edaItem.js"; //все Item о которых рассказывается 
import objEdaRecomend from "../../asset/obj/eda/edaRecomend.js"; //рекомендации образования
import objRecommendationSwiper from "../../asset/obj/recommendationSwiper.js"; //рекомендуемые подборки из категорий 

// componets
import ParagrafHome from "../../componets/paragrafHome/ParagrafHome";
//section
import Aside from "../../section/aside/Aside.jsx";
import RecommendationSwiper from "../../section/recommendationSwiper/RecommendationSwiper.jsx";
//style
// import "./education.scss"

export default function Food(){
    return(
        <div className="_container">
        <div className="build__wrapper ">
            <div className="build__container">
                <div className="build__minGrid _body__box__shadow">
                    <ParagrafHome title="Еда в Димитровграде"/>
                    <div className="build__minGrid__container">
                        {bilbordEda?.map((item) => (
                        <Link key={item.id} to={item.link}><div className="build__minGrid__item cartMainPage schadowImg" style={{ backgroundImage: `url(${item.img})` }}><a href="google.com"><h3>{item.title}</h3></a></div></Link>
                        ))}
                    </div>
                </div>
                <RecommendationSwiper props={objEdaRecomend}/>
                <div className="build__content">
                    {objEdaItem?.map((item) => (
                        <Link to={item.link} className="build__content__item" key={item.id}>
                            <div className="build__content__item__images"><img src={item.img} alt="" /></div>
                            <div className="build__content__item__content">
                            <h4>{item.title}</h4>
                            <p>{item.subtitle}</p>
                            <h5>{item.data}</h5>
                            <h2>{item.name}</h2>
                            <h6>{item.price}</h6>
                            <a className="button__buy" href="gogle.com">Узнать подробнее</a>
                            </div>
                        </Link>
                    ))}
                </div>
                <RecommendationSwiper props={objRecommendationSwiper}/>

            </div>
        </div>
        <div className="buildAside__container">
            <Aside props={objAside}/>
        </div>
        </div>
    )
}